.card {
    width: 260px; 
}



.name-rating h4 {
    color: #e89e00;
}

a {
    text-decoration: none;
}
.name {
    opacity: 0;
    width:260px;
    -webkit-transition: all .05s ease-in-out;
    transition: all .05s ease-in-out;
    text-align: left;
    height: 40px;
    align-items: flex-end;
    color: #c4c2c2;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), color-stop(rgba(0, 0, 0, 0.7)), to(rgba(0, 0, 0, 1)));
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 1));
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px)
}

.name h3 {
    padding: 10px 0 0 5px;
    font-size: 0.8em;
}

.card:hover img {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    cursor: pointer;
}

.card img {
    height: 113px;
    width: 200px;
    z-index: 1;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

 .card:hover .name {
    opacity: 1;
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
    
}


#cardlist {
    display: grid;
    grid-template-columns: repeat( auto-fill, minmax(210px, 1fr) );
    justify-content: center;
    justify-items: center;
    
}
body {
  height: 100%;
  text-align: center;
  background-color: #282c34;
  font-family: 'ABeeZee', sans-serif;
}

h1 {
  margin-top: 5%;
  color: #e89e00;
  font-size: 3em;
  
}

.section-header {
  color: #f2f2f2;
  margin-bottom: 5%;
}
.footer p {
color: #f2f2f2
}

.footer p a {
color: #e89e00
}
nav {
    margin: 30px ;
    display: flex;
    justify-content: flex-end
}

nav div {
    margin-left: 10px;
    color: #f2f2f2;
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
}

nav div:hover {
    cursor: pointer;
    color: #e89e00;
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
}
.movie {
    display: flex;
    text-align: left;
    color: #a0a0a0;
}

.description {
    margin: 0 5%
}

.description h5 {
    color: #3DB55A
}

.name-rating {
    text-align: right;
    color: #e89e00;
}


.description input, .description button {
background: #edd7aa;
border-radius: 5px;
border: 2px solid #e89e00;
margin: 2px;
}

@media only screen and (max-width: 768px) {
    .movie img {
        height: 200px;
        width: 150px;
    }

    .movie p {
        font-size: 0.8em;
    }
}
